import { Input } from "antd";
import { useEffect, useState } from "react";
import "./App.css";

function App() {
  const [field, setField] = useState(4);
  const [input, setInput] = useState("");
  const [data, setData] = useState([]);

  const generateHeaders = () => {
    let headers = ["", ""];
    for (let i = 1; i <= field - 3; i++) {
      headers.push(`${i}`);
    }
    headers.push("");
    return headers;
  };

  const calculateColumnSums = () => {
    const sums = Array(field).fill(0);
    data.forEach((row) => {
      for (let i = 2; i < row.length; i++) {
        sums[i] = (sums[i] || 0) + (parseInt(row[i].replace(/,/g, "")) || 0);
      }
    });
    return sums;
  };

  useEffect(() => {
    let inputList = input
      .trim()
      .split(" ")
      .filter((w) => w !== "");

    let rows = [];

    while (inputList.length && field > 0) {
      rows.push(inputList.splice(0, field));
    }

    setData(rows);
  }, [input, field, setData]);

  const columnSums = calculateColumnSums();

  return (
    <div className="App">
      <div>
        
        <Input
          placeholder=""
          defaultValue={4}
          type="number"
          min={4}
          onChange={(e) => setField(e.target.value)}
        />
        
        <Input
          placeholder=""
          onChange={(e) => setInput(e.target.value)}
        />
      </div>

      <div style={{ padding: "10px", textAlign: "center" }}>
        <table border={"1 dashed"}>
          <thead>
            <tr>
              {generateHeaders().map((header, index) => (
                <th key={index}>{header}</th>
              ))}
            </tr>
          </thead>
          <tbody>
            {data.map((row, index) => (
              <tr key={index}>
                {row.map((cell, cellIndex) => (
                  <td key={cellIndex}>{cell}</td>
                ))}
              </tr>
            ))}
            <tr>
              <td colSpan={2} style={{ color: "red" }}>
                
              </td>
              {columnSums.slice(2, field).map((sum, index) => (
                <td style={{ color: "red" }} key={index}>
                  {sum.toLocaleString()}
                </td>
              ))}
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  );
}

export default App;
